      const totalLoad = totalSection1Load + totalSection2Load + totalSection3Load;
      const lossRate = totalLoad > 0 ? totalLoss / totalLoad * 100 : 0;

      // ++/
      const lineLossRate = totalGridPower > 0 ? totalLoss / totalGridPower * 100 : 0;

